* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  text-decoration: none;
  transition: 1s;
  list-style: none;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  outline: none;
}

.NavLinks li {
  color: #fff;
  text-transform: capitalize;
  font-size: medium;
  font-weight: 600;
  padding: 12px;
  cursor: pointer;
  transition: 0.5s;
}

.NavLinks .active {
  border-bottom: 1px solid red;
  padding: 12px 0;
}

.NavLinks li:hover {
  color: red;
}

body {
  min-height: 90vh;
}

.slick-slide > div {
  margin: 0 10px;
}

section {
  padding: 50px;
  margin-top: 5%;
}

@media (max-width: 768px) {
  section {
    padding: 20px;
    margin-top: 5%;
  }
}
header {
  min-height: "90vh";
}

.header::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(26, 25, 25, 0.5);
}

.HeroText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 12px, 24px;
  text-align: center;
  align-items: center;
  align-content: center;
  font-family: "Lucida Console", "Courier New", monospace;
}

.herobtn {
  text-align: center;
  font-weight: "600";
  color: #fff;
  padding: 12px 24px;
  border: 1px solid;
  border-radius: 5px;
  background-color: black;
  text-transform: uppercase;
  margin-top: 17px;
  width: 50%;
  outline: none;
  transition: 0.5s;
  cursor: pointer;
}

.herobtn:hover {
  background-color: transparent;
}

.Heading::after {
  content: "";
  position: absolute;
  width: 45px;
  height: 2px;
  background-color: #000;
  margin-top: 15px;
  margin-left: 3px;
}

@media (max-width: 768px) {
  .NavLinks {
    flex-direction: column;
    gap: 10px;
    position: absolute;
    background-color: #D4D4D4;
    top: 0;
    width: 65%;
    text-align: center;
    align-items: center;
    height: auto;
    
  }
.NavLinks li{
  padding: 3rem 1rem;

}
  

  header {
    min-height: "40vh";
  }

  .HeroText {
    left: 5%;
    top: 15%;
    transform: translate(0, 0);
    padding: 4px 8px;
  }
  .HeroText {
    top: 30%;
    left: 10%;
  }

  .header {
    min-height: 65vh;
  }

  .Heading::before {
    content: "";
    position: absolute;
    width: 45px;
    height: 2px;
    background-color: red;
    margin-top: 30px;
    margin-left: 0;
  }
  .Heading::after {
    display: none;
  }
}

.category-btn {
  font-weight: bold !important;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: transparent;
  outline: none;
  border: none;
  text-align: center;
  padding: 7px 24px;
  margin: 14px;
  border-radius: 20px;
  font-size: medium;
  transition: all 0.3s ease;
  opacity: 0.7;
}

.typing-animation {
  overflow: hidden;
  border-right: 0.15em solid orange;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  animation: typing 1.25s steps(40, end), blink-caret 0.75s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

.FashionCard,
.OverviewCard {
  max-width: 100%;
}
.OverviewCard {
  transition: all 0.3s ease;
}

.OverviewCard:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .FashionCardImg {
    height: auto;
  }
}
